
import { defineComponent } from 'vue';
import CongratulationFrame from "@/components/CongratulationFrame.vue";
const start_position = 1;

const cards_generic_data = [{main_context: "–ó–¥–µ—Å—å –º–æ–≥–ª–∞ –±—ã—Ç—å –≤–∞—à–∞", signature: "–ê–±–æ–±–∞"},
                            {main_context: "–ó–¥–µ—Å—å —Ç–æ–∂–µ", signature: "–≥–µ–Ω–µ—Ä–∞–ª –ì–∞–≤—Å"},
                            {main_context: "–ê –∑–¥–µ—Å—å?", signature: "–§–§–§–§"}]

const generic_data_length = cards_generic_data.length;



export default defineComponent({
    name: 'FrameCarousel',
    props: {},
    components: {
